.EditPage {
  height: 100%;
  overflow-y: hidden;
}
.EditPage .ant-divider-horizontal {
  margin: 0px !important;
}
.EditPage .ant-layout-content {
  display: flex;
  flex-direction: row;
}
.EditPage .leftPannel {
  height: 100%;
}
.EditPage .leftPannel1 {
  height: 100%;
}
.EditPage .leftSymmetryPannel {
  height: 100% !important;
  background-color: white !important;
  z-index: 3 !important;
  position: relative;
}
.EditPage .ant-layout-sider-zero-width-trigger {
  background: transparent !important;
  position: absolute;
  top: calc(50vh - 50px);
  height: 60px;
  padding-top: 2px;
}
.EditPage .ant-layout-sider-zero-width .trigger1 {
  margin-left: 0px;
}
.EditPage .ant-layout-sider-zero-width .trigger2 {
  margin-right: 0px;
}
.EditPage .trigger1 {
  background: #FFFFFF;
  color: #484F5D;
  box-shadow: 2px 0 5px 0 rgba(133, 133, 133, 0.5);
  border-radius: 0px 10px 10px 0px;
  height: 42px;
  width: 22px;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0px;
}
.EditPage .trigger2 {
  background: #FFFFFF;
  color: #484F5D;
  box-shadow: -2px 0 5px 0 rgba(133, 133, 133, 0.5);
  border-radius: 10px 0px 0px 10px;
  height: 42px;
  width: 22px;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 13px;
}
.EditPage .splitPane-box {
  height: calc(100% - 50px) !important;
}
.EditPage .SplitPane.vertical {
  height: calc(100% - 50px) !important;
}
.EditPage .Resizer {
  background: #000;
  opacity: 0.2;
  z-index: 1;
  box-sizing: border-box;
  -moz-background-clip: padding;
  -webkit-background-clip: padding;
  background-clip: padding-box;
}
.EditPage .Resizer:hover {
  transition: all 2s ease;
}
.EditPage .Resizer.horizontal {
  height: 11px;
  margin: -5px 0;
  border-top: 5px solid rgba(255, 255, 255, 0);
  border-bottom: 5px solid rgba(255, 255, 255, 0);
  cursor: row-resize;
  width: 100%;
}
.EditPage .Resizer.horizontal:hover {
  border-top: 5px solid rgba(0, 0, 0, 0.5);
  border-bottom: 5px solid rgba(0, 0, 0, 0.5);
}
.EditPage .Resizer.vertical {
  width: 11px;
  margin: 0 -5px;
  border-left: 5px solid rgba(255, 255, 255, 0);
  border-right: 5px solid rgba(255, 255, 255, 0);
  cursor: col-resize;
}
.EditPage .Resizer.vertical:hover {
  border-left: 5px solid rgba(0, 0, 0, 0.5);
  border-right: 5px solid rgba(0, 0, 0, 0.5);
}
.EditPage .Resizer.disabled {
  cursor: not-allowed;
}
.EditPage .Resizer.disabled:hover {
  border-color: transparent;
}
.EditPage .editStroyview {
  height: 100%;
}
.ant-spin-nested-loading > div > .ant-spin {
  float: left;
}
